#gameNameInput {
  font-family: "Roboto", sans-serif;
  border: none;
  border-bottom: 2px solid #fff;
  border-radius: 0;
  width: 100%;
  height: 50px;
  font-size: 30px;
  font-weight: 200;
  padding-left: 10px;
  background-color: rgba(0, 0, 0, 0);
  margin-bottom: 50px;
  margin-top: 50px;
  color: #fff;
}

.fa-search {
  font-size: 130%;
  margin-top: 55px;
  margin-left: 10px;
}

.button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  color: #fff;
  font-size: 30px;
  font-weight: lighter;
}

.button:focus {
  outline: none;
}

.cursor {
  position: relative;
  float: left;
  width: 88%;
}
.cursor i {
  position: absolute;
  width: 2px;
  height: 26%;
  background-color: #fff;
  left: 10px;
  top: 53px;
  -webkit-animation-name: blink;
          animation-name: blink;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  opacity: 1;
}

.cursor input:focus + i {
  display: none;
}

@-webkit-keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes blink {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}



