.nav-item,
.nav-link a {
  margin: 5px;
  padding-right: 10px;
  font-size: 11pt;
  color: #fff;
  font-weight: lighter;
  float: right;
}

Navbar {
  height: 20px auto;
  background-color: #222;
  color: #fff;
}

.nav-button button,
.nav-button {
  background-color: #79e;
  height: 30px;
  font-size: 11pt;
  border-radius: 3px;
  color: #fff;
  border: none;
  text-align: center;
}



